___CSS_LOADER_EXPORT___.push([module.id, "\n.title {\n  font-size: 50px;\n}\n.iotApp{\n  position: fixed;\n  height: 100%;\n  width: 100%;\n  background-color: #101720;\n}\n.iot-content{\n  position: fixed;\n  padding: 0px 3% 13% 3%;\n  height: 80%;\n  width: 94%;\n   overflow: scroll;\n}\n.physicalUnionApplication-card{\n  margin-top: 5%;\n}\n.physicalUnionApplication-list-card{\n    margin-bottom: 5%;\n}\n.environmentalMonitoring-card{\n  margin-top: 5%;\n}\n.towerCraneMonitoring-card{\n  margin-top: 5%;\n}\n.header-conditions{\n  width: 94%;\n  height: 50px;\n  display: flex;\n  flex-direction: row;\n  padding: 0px 3%;\n  vertical-align: middle;\n  position: relative;\n  top: -1;\n  background-color: #101720;\n}\n.header-picker{\n  width: 30%;\n  height: 100%;\n  display: inline-block;\n  background-color: transparent;\n}\n.header-input{\n  width: 60%;\n  height: 100%;\n  display: flex;\n  flex-direction: row;\n  vertical-align: middle;\n}\n.chang-list-type{\n  width: 10%;\n  height: 100%;\n  display: flex;\n  align-items: center;\n}\n.list-type-img{\n  width: 21px;\n  height: 19px;\n}\n\n", "",{"version":3,"sources":["webpack://./src/views/aiot/iotApp.vue"],"names":[],"mappings":";AA0SA;EACA,eAAA;AACA;AACA;EACA,eAAA;EACA,YAAA;EACA,WAAA;EACA,yBAAA;AAEA;AACA;EACA,eAAA;EACA,sBAAA;EACA,WAAA;EACA,UAAA;GACA,gBAAA;AACA;AACA;EACA,cAAA;AACA;AACA;IACA,iBAAA;AACA;AACA;EACA,cAAA;AACA;AACA;EACA,cAAA;AACA;AACA;EACA,UAAA;EACA,YAAA;EACA,aAAA;EACA,mBAAA;EACA,eAAA;EACA,sBAAA;EACA,kBAAA;EACA,OAAA;EACA,yBAAA;AACA;AACA;EACA,UAAA;EACA,YAAA;EACA,qBAAA;EACA,6BAAA;AACA;AACA;EACA,UAAA;EACA,YAAA;EACA,aAAA;EACA,mBAAA;EACA,sBAAA;AACA;AACA;EACA,UAAA;EACA,YAAA;EACA,aAAA;EACA,mBAAA;AACA;AAEA;EACA,WAAA;EACA,YAAA;AACA","sourcesContent":["<template>\n  <div class=\"iotApp\">\n    <!-- 顶部条件选择 start -->\n    <div class=\"header-conditions\">\n      <div class=\"header-picker\">\n        <van-dropdown-menu>\n          <van-dropdown-item v-model=\"thisSubsystemId\" :options=\"subsystemList\" @closed=\"changeSystem()\" />\n        </van-dropdown-menu>\n      </div>\n      <div class=\"header-input\">\n        <van-search v-model=\"queryCondition\" placeholder=\"设备名称\\IEMI码\" :clearable=\"false\" @search=\"onSearch\" />\n      </div>\n      <div class=\"chang-list-type\">\n        <img v-show=\"!isCard\" src=\"@/assets/images/public/list-type-table.png\" alt=\"\" class=\"list-type-img\" @click=\"changeListType\">\n        <img v-show=\"isCard\" src=\"@/assets/images/public/list-type-card.png\" alt=\"\" class=\"list-type-img\" @click=\"changeListType\">>\n      </div>\n    </div>\n    <!-- end -->\n    <!-- 内容 start -->\n    <div class=\"iot-content\">\n      <!-- 卡片展示列表 start -->\n      <van-loading v-if=\"loadding\" size=\"24px\" vertical>\n        加载中...\n      </van-loading>\n      <div v-if=\"isCard\">\n        <div v-if=\"thisSubsystemId===5 && equipInfoList.length>0\" class=\"show-list\">\n          <van-list\n            v-model=\"loading\"\n            :finished=\"finished\"\n            finished-text=\"没有更多了\"\n            class=\"van-clearfix\"\n            :immediate-check=\"false\"\n            @load=\"getEquipInfoList\"\n          >\n            <Adaptive v-for=\"item in equipInfoList\" :key=\"item.index\" :data=\"['100%','49.9%']\" class=\"physicalUnionApplication-card\">\n              <PhysicalUnionApplication :data=\"item\" />\n            </Adaptive>\n          </van-list>\n        </div>\n        <div v-if=\"thisSubsystemId===10 && equipInfoList.length>0\" class=\"show-list\">\n          <van-list\n            v-model=\"loading\"\n            :finished=\"finished\"\n            finished-text=\"没有更多了\"\n            class=\"van-clearfix\"\n            :immediate-check=\"false\"\n            @load=\"getEquipInfoList\"\n          >\n            <Adaptive v-for=\"item in equipInfoList\" :key=\"item.index\" :data=\"['100%','77.9%']\" class=\"environmentalMonitoring-card\">\n              <EnvironmentalMonitoring :data=\"item\" />\n            </Adaptive>\n          </van-list>\n        </div>\n        <div v-if=\"thisSubsystemId===11 && equipInfoList.length>0\" class=\"show-list\">\n          <van-list\n            v-model=\"loading\"\n            :finished=\"finished\"\n            finished-text=\"没有更多了\"\n            class=\"van-clearfix\"\n            :immediate-check=\"false\"\n            @load=\"getEquipInfoList\"\n          >\n            <TowerCraneMonitoring v-for=\"(item, index) in equipInfoList\" :key=\"index\" class=\"towerCraneMonitoring-card\" :data=\"item\" />\n          </van-list>\n        </div>\n\n        <div v-if=\"equipInfoList.length===0 && !loadding\" class=\"nothing-list\">\n          <img src=\"@/assets/images/public/nothing.png\" alt=\"\" class=\"nothing-img\">\n          <div class=\"nothing-content\">\n            无匹配项\n          </div>\n        </div>\n      </div>\n      <!-- end -->\n      <!-- 列表卡片 start -->\n      <div v-if=\"!isCard\">\n        <div v-if=\" equipInfoList.length>0\" class=\"show-list\">\n          <van-list\n            v-model=\"loading\"\n            :finished=\"finished\"\n            finished-text=\"没有更多了\"\n            class=\"van-clearfix\"\n            :immediate-check=\"false\"\n            @load=\"getEquipInfoList\"\n          >\n            <div v-for=\"item in equipInfoList\" :key=\"item.index\" @click=\"toDetailInfo(item.equipId)\">\n              <Adaptive :data=\"['100%','31.39%']\" class=\"physicalUnionApplication-list-card\">\n                <PhysicalUnionApplicationListCard :data=\"item\" />\n              </Adaptive>\n            </div>\n          </van-list>\n        </div>\n        <div v-if=\"equipInfoList.length===0 && !loadding\" class=\"nothing-list\">\n          <img src=\"@/assets/images/public/nothing.png\" alt=\"\" class=\"nothing-img\">\n          <div class=\"nothing-content\">\n            无匹配项\n          </div>\n        </div>\n      </div>\n      <!-- end -->\n    </div>\n    <!-- end -->\n  </div>\n</template>\n\n<script>\nimport PhysicalUnionApplication from 'cmp/equipCard/PhysicalUnionApplication.vue'\nimport PhysicalUnionApplicationListCard from 'cmp/equipListCard/EquipListCard.vue'\nimport EnvironmentalMonitoring from 'cmp/equipCard/EnvironmentalMonitoring.vue'\nimport TowerCraneMonitoring from 'cmp/equipCard/TowerCraneMonitoring.vue'\nimport ReadTypeNameOnVuex from '@/utils/readTypeNameOnVuex'\nimport VueDataLoading from 'vue-data-loading'\n\nimport Api from '@/api/aiot/iotApp.js'\n\nexport default {\n  components: {\n    // 智慧物联设备信息卡片\n    PhysicalUnionApplication,\n    // 设备信息列表卡片  子系统通用\n    PhysicalUnionApplicationListCard,\n    // 环境监测设备信息卡片\n    EnvironmentalMonitoring,\n    // 塔机检测设备信息卡片\n    TowerCraneMonitoring,\n    VueDataLoading\n  },\n  data() {\n    return {\n      loading: false,\n      loadding: true,\n      // 系统选择下拉菜单\n      subsystemList: [\n        { text: '智慧视觉', value: 5 },\n        { text: '环境监测', value: 10 },\n        { text: '塔机监测', value: 11 }\n      ],\n      // 当前选中系统\n      thisSubsystemId: 5,\n      // 展示形式 （列表||卡片）\n      isCard: false,\n      // 查询条件 （这里只有模糊查询）\n      queryCondition: '',\n      // 设备卡片信息\n      equipInfoList: [],\n      page: 0,\n      finished: false\n    }\n  },\n  created() {\n    // 渲染页面查询卡片列表片数据\n    if (this.$route.query.systemId) {\n      this.thisSubsystemId = Number(this.$route.query.systemId)\n      console.log('带参跳转')\n    }\n    this.getEquipInfoList()\n  },\n\n  methods: {\n    /**\n     * 查询\n     */\n    onSearch(e) {\n      this.page = 0\n      this.finished = false\n      this.loading = false\n      this.equipInfoList = []\n      this.getEquipInfoList()\n    },\n    toDetailInfo(equipId) {\n      this.$router.push({\n        path: '/iotAppDetail',\n        query: {\n          id: equipId\n        }\n      })\n    },\n    /**\n     * 输入框内容变化改变\n     */\n    conditionChange(value) {\n      this.queryCondition = value\n    },\n    /**\n     * 切换子系统11\n     */\n    changeSystem() {\n      this.page = 0\n      this.finished = false\n      this.loading = false\n      this.equipInfoList = []\n      this.getEquipInfoList()\n    },\n    /**\n     * 点击切换数据展示形式 （列表||卡片）\n     */\n    changeListType() {\n      this.finished = false\n\n      this.isCard = !this.isCard\n    },\n    // infiniteScroll() {\n    //   // 到底触发的事件\n    //   console.log('到底触发的事件')\n    //   this.page++\n    //   // this.$emit('changeData', this.page)\n    // },\n\n    /**\n     * 获取卡片列表\n     */\n    async getEquipInfoList() {\n      // this.loading = true\n      this.loadding = true\n      let temp = []\n      this.loading = false\n      const params = {\n        systemType: this.thisSubsystemId,\n        page: ++this.page,\n        size: 10,\n        conditionStr: (this.queryCondition.length < 1 ? '' : '?condition=' + this.queryCondition)\n      }\n\n      const res = await Api.equipInfoList(params)\n      const total = res.data.total\n\n      if (res.code === 200) {\n        temp = [...res.data.rows]\n        if (params.page > total) {\n          this.finished = true\n          this.loadding = false\n          this.equipInfoList = this.equipInfoList.concat(temp)\n          return\n        }\n        if (temp.length === 0) {\n          console.log('长度为0')\n          this.equipInfoList = temp\n          this.loadding = false\n          this.finished = true\n          return\n        }\n      }\n\n      console.log('设备列表', [...res.data.rows])\n      // 获取设备列表集合的\n      const ids = temp.map(item => { return item.equipId })\n\n      // 获取设备列表ids对应的未处理事件数\n      const hazardCountList = await Api.equipUntreatedEventList(ids)\n\n      // 根据设备类型id获取对应的设备类型名称\n      temp = await ReadTypeNameOnVuex.conversion('equipType', 'equipType', 'equipTypeName', temp)\n      let combined = []\n      if (hazardCountList.code === 200) {\n        combined = hazardCountList.data.reduce((acc, cur) => {\n          const target = acc.find(e => e.equipId === cur.equipId)\n          if (target) {\n            Object.assign(target, cur)\n          }\n          return acc\n        }, temp)\n      }\n      temp = combined\n\n      // 如果是环境监测或者是塔机监测就需要吧详细数据加载进来\n      if (this.thisSubsystemId === 10 || this.thisSubsystemId === 11) {\n        this.loadding = true\n        // 根据对应的设备类型去请求相对应系统的接口\n        let system = ''\n        if (this.thisSubsystemId === 10) {\n          system = 'environment'\n        } else if (this.thisSubsystemId === 11) {\n          system = 'tower'\n        }\n        // 获取ids去查询对应设备的详细数据\n        const realTimeList = await Api.equipRealTimeInfoList(ids, system)\n        // 把两个数组对象根据equipId来合并\n        if (realTimeList.code === 200) {\n          combined = realTimeList.data.reduce((acc, cur) => {\n            const target = acc.find(e => e.equipId === cur.equipId)\n            if (target) {\n              Object.assign(target, cur)\n            }\n            return acc\n          }, temp)\n        }\n        temp = combined\n      }\n      console.log('设备列表', temp)\n      this.equipInfoList = this.equipInfoList.concat(temp)\n\n      this.loadding = false\n    }\n  }\n}\n</script>\n\n<style>\n.title {\n  font-size: 50px;\n}\n.iotApp{\n  position: fixed;\n  height: 100%;\n  width: 100%;\n  background-color: #101720;\n\n}\n.iot-content{\n  position: fixed;\n  padding: 0px 3% 13% 3%;\n  height: 80%;\n  width: 94%;\n   overflow: scroll;\n}\n.physicalUnionApplication-card{\n  margin-top: 5%;\n}\n.physicalUnionApplication-list-card{\n    margin-bottom: 5%;\n}\n.environmentalMonitoring-card{\n  margin-top: 5%;\n}\n.towerCraneMonitoring-card{\n  margin-top: 5%;\n}\n.header-conditions{\n  width: 94%;\n  height: 50px;\n  display: flex;\n  flex-direction: row;\n  padding: 0px 3%;\n  vertical-align: middle;\n  position: relative;\n  top: -1;\n  background-color: #101720;\n}\n.header-picker{\n  width: 30%;\n  height: 100%;\n  display: inline-block;\n  background-color: transparent;\n}\n.header-input{\n  width: 60%;\n  height: 100%;\n  display: flex;\n  flex-direction: row;\n  vertical-align: middle;\n}\n.chang-list-type{\n  width: 10%;\n  height: 100%;\n  display: flex;\n  align-items: center;\n}\n\n.list-type-img{\n  width: 21px;\n  height: 19px;\n}\n\n</style>\n\n<style >\n.iotApp .van-search {\n  display: -webkit-box;\n  display: -webkit-flex;\n  display: flex;\n  -webkit-box-align: center;\n  -webkit-align-items: center;\n  vertical-align: middle;\n  align-items: center;\n  box-sizing: border-box ;\n  /* padding: 10px 12px; */\n  background-color: transparent;\n}\n.iotApp .van-search__content{\n  background-color: #101720;\n  padding-left: 0px;\n}\n\n.iotApp .van-dropdown-menu__bar{\n  background-color:  #101720 ;\n}\n.iotApp .van-dropdown-menu__title{\n  font-size: 15px;\n  font-family: PingFang SC;\n  font-weight: 400;\n  color: #8BA3C2;\n}\n.iotApp .van-field__control{\n  font-size: 15px;\n  font-family: PingFang SC;\n  font-weight: 400;\n  color: #8BA3C2\n}\n.iotApp .van-cell{\n  color: rgba(139, 163, 194, 1);\n  padding: 10px 31px;\n}\n.iotApp input::-webkit-input-placeholder{\n  color: #373F4A !important;\n\nfont-size: 12px\n}\n.iotApp .van-cell{\n  background-color: rgba(16, 23, 32, 1);\n}\n.iotApp .van-popup{\n  background-color: rgba(16, 23, 32, 1);\n\n}\n.iotApp .van-cell::after {\n  border-bottom: 1px solid rgba(16, 23, 32, 1);\n}\n.iotApp .van-dropdown-item__option--active .van-dropdown-item__icon{\n  color: rgba(139, 163, 194, 1);\n}\n.nothing-list{\n  width: 79%;\n  height: 100%;\n  position: fixed;\n  text-align: center;\n  line-height: 300%;\n  margin-left: 10%;\n}\n.nothing-img{\n  width: 90px;\n  height: 85px;\n  vertical-align: middle;\n  margin-top: 60%;\n}\n.nothing-content{\n  width: 78%;\n  height:auto;\n  font-size: 14px;\n  font-family: PingFang SC;\n  font-weight: 400;\n  color: #6F85A2;\n  margin-left: 6%;\n  margin-top: 2%;\n}\n</style>\n"],"sourceRoot":""}]);
